import 'core-js/features/map';
import 'core-js/features/set';

import React from 'react';
import ReactDOM from 'react-dom';

import bridge from '@vkontakte/vk-bridge';
import App from './App';

// Init VK Mini App
bridge.send('VKWebAppInit');

bridge.subscribe(({ detail: { type, data }}) => {

  if (type === 'VKWebAppUpdateConfig') {
    const schemeAttribute = document.createAttribute('scheme');
    schemeAttribute.value = data.scheme ? data.scheme : 'space_gray';
    document.body.attributes.setNamedItem(schemeAttribute);

    if (bridge.supports('VKWebAppSetViewSettings')) {
      const isDarkTheme = data.scheme === 'space_gray';
      bridge.send('VKWebAppSetViewSettings', {
        'status_bar_style': isDarkTheme ? 'light' : 'dark',
        'action_bar_color': isDarkTheme ? '#ffffff' : '#000000',
        'navigation_bar_color': isDarkTheme ? '#fff' : '#000'
      });
    }
  }
});

ReactDOM.render(<App />, document.getElementById('root'));