import React, { useState, useEffect } from 'react';

import axios from 'axios';
import bridge from '@vkontakte/vk-bridge';

import {
	Panel,
    PanelHeader,
    PanelSpinner,
    Group,
    CellButton,
    Footer,
    List,
    Link,
    Cell,
    Avatar
} from '@vkontakte/vkui/dist/';

import {
    Icon28ChainOutline
} from '@vkontakte/icons/dist/';
import {Snackbar} from "@vkontakte/vkui";
import {Icon16Done} from "@vkontakte/icons";

const Affilate = props => {

    const [data, setData] = useState(null);
    const [isLoading, setLoading] = useState(true);
    const [snackbar, setSnackbar] = useState(true);

    useEffect(() => {
        
        async function fetch() {

            const request = await axios.post(`${props.server}/getAffilateData`, {
				sign: window.location.search
			});

            if (typeof request.data !== 'object') return;

            if (request.data.response.length < 1) {
                setData([]);
                return setLoading(false);
            }

			const token = await bridge.send('VKWebAppGetAuthToken', {
				'app_id': props.app_id,
				'scope': ''
			});

			if (!token) return;

			const string = request.data.response.join(',');

			const users = await bridge.send('VKWebAppCallAPIMethod', {
				'method': 'users.get',
				'request_id': 'fetchUsers',
				'params': {
					'user_ids': string,
					'fields': 'photo_200',
					'v': '5.124',
					'access_token': token.access_token
				}
            });

            setData(users.response);
			setLoading(false);
        }

        fetch();

    }, [ props.server, props.app_id ]);

	return (
		<Panel id={props.id}>

			<PanelHeader>–†–µ—Ñ–µ—Ä–∞–ª—å–Ω–∞—è –ø—Ä–æ–≥—Ä–∞–º–º–∞</PanelHeader>
            {isLoading && <PanelSpinner />}

			{!isLoading &&
				<div>

                    <Group>
                        <Footer>–° –∫–∞–∂–¥–æ–≥–æ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ —Ä–µ–∫–ª–∞–º—ã —Ä–µ—Ñ–µ—Ä–∞–ª–æ–º<br />–í—ã –ø–æ–ª—É—á–∞–µ—Ç–µ 2üí∞</Footer>
                        <CellButton before={<Icon28ChainOutline width={24} height={24} />} onClick={
                            () => {
                                bridge.send('VKWebAppCopyText', {'text': `https://vk.com/app${props.app_id}#join${props.fetchedUser.id}`});
                                setSnackbar(
                                    <Snackbar
                                        layout="vertical"
                                        onClose={() => setSnackbar(null)}
                                        before={<Avatar size={24} style={{ background: 'var(--dynamic_green)' }}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
                                    >
                                        –°—Å—ã–ª–∫–∞ —Å–∫–æ–ø–∏—Ä–æ–≤–∞–Ω–∞
                                    </Snackbar>
                                );
                            }
                        }
                        >–°–∫–æ–ø–∏—Ä–æ–≤–∞—Ç—å —Å—Å—ã–ª–∫—É</CellButton>
                    </Group>
                    {snackbar}

                    {data.length < 1 && <Footer>–£ –í–∞—Å –Ω–µ—Ç —Ä–µ—Ñ–µ—Ä–∞–ª–æ–≤.</Footer>}

                    <List>
                        {data.length > 0 && data.map((e, i) => {
                            return (
                                <Link key={i} href={'https://vk.com/id' + e.id} target='_blank'>
                                    <Cell before={<Avatar src={e.photo_200} />}>{e.first_name} {e.last_name}</Cell>
                                </Link>
                            )
                        })}
                    </List>

                </div>
            }

		</Panel>
	);
}

export default Affilate;
