import React, { useState, useEffect } from 'react';

import axios from 'axios';
import bridge from '@vkontakte/vk-bridge';

import {
	Panel,
	PanelHeader,
	PanelSpinner,
	FixedLayout,
	Group,
	CellButton,
	Footer,
	CardGrid,
	Card,
	Cell,
	Avatar,
	Snackbar,
	ScreenSpinner
} from '@vkontakte/vkui/dist/';

import {
	Icon24Add,
	Icon16Done,
	Icon16Cancel,
	Icon24LogoVk,
	Icon28ArrowUpOutline
} from '@vkontakte/icons/dist/';
import {PanelHeaderButton} from "@vkontakte/vkui";

const isQiwi = type => {
	if (type === 'qiwi') return true;
	else return false;
}

const separateInt = (int) => {
	return int.toString().replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ').replace('.', ',');
}

const Withdraw = props => {

	const [withdraws, setWithdraws] = useState(null);
	const [isLoading, setLoading] = useState(true);

	const [snackbar, setSnackbar] = useState(null);

	useEffect(() => {

		async function fetch() {

			const request = await axios.post(`${props.server}/getWithdraws`, {
				sign: window.location.search
			});

			if (request.data.error) return window.location.reload();

			setWithdraws(request.data.response);
			setLoading(false);

		}

		fetch();

	}, [ props ]);

	async function openModal() {
		props.setModalData(null);

		const isAllow = await bridge.send('VKWebAppAllowMessagesFromGroup', {'group_id': props.group_id, 'key': Date.now().toString()}).catch(e => {
			if (e.error_data.error_reason === 'User denied') return;
		});

		if (!isAllow) return;

		props.setActiveModal('new withdraw');
	}

	return (
		<Panel id={props.id}>

			<PanelHeader
				left={<PanelHeaderButton><Icon24Add /></PanelHeaderButton>} onClick={() => openModal()}
			>–í—ã–≤–æ–¥</PanelHeader>
			{isLoading && <PanelSpinner />}

			{!isLoading &&
				<div>

					{withdraws && withdraws.length < 1 && <Footer>–£ –í–∞—Å –µ—â—ë –Ω–µ—Ç –≤—ã–ø–ª–∞—Ç.</Footer>}

					{withdraws && withdraws.length > 0 &&
					    <Group separator='hide'>
							<CardGrid>
								{withdraws.map((e, i) => {
									return (
										<Card key={i} size='l' mode='shadow'>
											<div style={{ height: 68 }}>
												<Cell 
													style={{ marginTop: 8 }}
													before={<Avatar><Icon28ArrowUpOutline /></Avatar>}
													description={e.toWallet}
													indicator={e.is_deleted ? '–û—Ç–º–µ–Ω–µ–Ω–æ' : e.is_success ? <span style={{ color: 'var(--dynamic_green)' }}>–£—Å–ø–µ—à–Ω–æ</span> : '–û–∂–∏–¥–∞–Ω–∏–µ'}
												>
													–í—ã–≤–æ–¥ –Ω–∞ {isQiwi(e.toType) ? 'Qiwi' : '–±–∞–Ω–∫–æ–≤—Å–∫—É—é –∫–∞—Ä—Ç—É'} {e.amount} —Ä—É–±.
												</Cell>
											</div>
										</Card>
									)
								})}
							</CardGrid>
						</Group>
					}
				</div>
			}

			{snackbar}

		</Panel>
	);
}

export default Withdraw;
