import React, { useState, useEffect } from 'react';

import axios from 'axios';
import bridge from '@vkontakte/vk-bridge';

import {
	Panel,
	PanelHeader,
	PanelSpinner,
	Div,
	Link,
	Cell,
	Avatar,
} from '@vkontakte/vkui/dist/';

const separateInt = (int) => {
	return int.toString().replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ').replace('.', ',');
}

const Scoreboard = props => {

	const [scoreboard, setScoreboard] = useState(null);
	const [isLoading, setLoading] = useState(true);

	useEffect(() => {

        async function fetch() {

            const request = await axios.post(`${props.server}/getScoreboard`, {
				sign: window.location.search
			});

            if (typeof request.data !== 'object') return;

			const token = await bridge.send('VKWebAppGetAuthToken', {
				'app_id': props.app_id,
				'scope': ''
			});

			if (!token) return;

			const ids = [];

			await request.data.response.forEach((e) => {
				ids.push(e.user_id.toString());
			});

			const string = ids.join(',');

			const users = await bridge.send('VKWebAppCallAPIMethod', {
				'method': 'users.get',
				'request_id': 'fetchUsers',
				'params': {
					'user_ids': string,
					'fields': 'photo_200',
					'v': '5.124',
					'access_token': token.access_token
				}
            });

			await request.data.response.map((user, i) => {
				user.vk = users.response[i];
				return user;
			});

            setScoreboard(request.data.response);
			setLoading(false);
        }

        fetch();

    }, [ props.server, props.app_id ]);

    const avatarWrapperStyle = {
		display: 'flex',
		flexDirection: 'row',
		paddingRight: 10
	}

    const numberStyle = {
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'center',
		marginRight: 20,
		fontSize: 20
    }

	return (
		<Panel id={props.id}>

			<PanelHeader>–¢–æ–ø</PanelHeader>
			{isLoading && <PanelSpinner />}

			{!isLoading &&
				<div>
					<Div>
						{scoreboard && scoreboard.map((e, i) => {
							return (
								<Link key={i} href={'https://vk.com/id' + e.user_id} target='_blank'>
									<Cell
										size={48}
										description={`${separateInt(e.earned)} üí∞`}
										before={
											<div style={avatarWrapperStyle}>
												<div style={numberStyle}><b>{i+1}</b></div>
												<Avatar src={e.vk.photo_200} />
											</div>
										}
									>
										{e.vk.first_name} {e.vk.last_name}
									</Cell>
								</Link>
							);
						})}
					</Div>
				</div>
			}

		</Panel>
	);
}

export default Scoreboard;
