import React, { useEffect, useState } from 'react';

import bridge from '@vkontakte/vk-bridge';
import axios from 'axios';
import md5 from 'md5';

import {
	Panel,
	PanelHeader,
	PanelSpinner,
	Placeholder,
	Button,
	Title,
	FixedLayout,
} from '@vkontakte/vkui/dist/';

import {
	Icon28ViewOutline
} from '@vkontakte/icons/dist/';
import {Icon20VotestTransferCircleFillTurquoise} from "@vkontakte/icons";
import logo from "../img/watermark.png"

const separateInt = (int) => {
	return int.toString().replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ').replace('.', ',');
}

const Home = props => {

	const [isLoading, setLoading] = useState(true);
	const [autoView, setAutoView] = useState(false);

	useEffect(() => {

		async function fetch() {

			if (props.fetchedData !== null) return setLoading(false);

			const request = await axios.post(`${props.server}/getUser`, {
				sign: window.location.search
			});

			if (request.data.error) return window.location.reload();

			props.setData(request.data.response);
			setLoading(false);

		}

		fetch();

	}, [ props ]);


	async function viewAd() {
		if (!bridge.supports('VKWebAppShowNativeAds')) return;

		bridge.send('VKWebAppShowNativeAds', { ad_format:'reward' })
		.catch(error => console.log('Error! ' + error));

		const key = md5(`${Math.floor(Math.random() * Math.floor(200))}-${Date.now()}-null`);

		const request = await axios.post(`${props.server}/viewAd`, {
			sign: window.location.search,
			key: `${key}-${Math.random()}-${Date.now()}`
		});

		if (request.data.error) return window.location.reload();

		return props.setData({...props.fetchedData, earned: props.fetchedData.earned + 2});

	}

	async function changeView(state) {
		setAutoView(state);
	}

	return (
		<Panel id={props.id}>

			<PanelHeader>–ó–∞—Ä–∞–±–æ—Ç–∞—Ç—å</PanelHeader>
			{isLoading && <PanelSpinner />}

			{!isLoading &&
				<div>

					<Placeholder
						stretched
						icon={<img src={logo} height={128} width={128}></img>}
						action= {
							bridge.supports('VKWebAppShowNativeAds')
								?
								(<Button before={<Icon28ViewOutline />} mode='outline' size='l' onClick={() => viewAd()}>–°–º–æ—Ç—Ä–µ—Ç—å —Ä–µ–∫–ª–∞–º—É</Button>)
								:
								(<Button mode='outline' size='l' disabled>–û—Ç–∫—Ä–æ–π—Ç–µ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ —á–µ—Ä–µ–∑ –∫–ª–∏–µ–Ω—Ç –í–ö–æ–Ω—Ç–∞–∫—Ç–µ.</Button>)
						}
						header= {'AdsMoney - –¥–µ–Ω—å–≥–∏ –∑–∞ –ø—Ä–æ—Å–º–æ—Ç—Ä —Ä–µ–∫–ª–∞–º—ã!'}
					>
						–°–º–æ—Ç—Ä–∏—Ç–µ —Ä–µ–∫–ª–∞–º—É<br />–∏ –ø–æ–ª—É—á–∞–π—Ç–µ 2üí∞ –∑–∞ –ø—Ä–æ—Å–º–æ—Ç—Ä.
					</Placeholder>

					<FixedLayout vertical='bottom'>
						<center>
							<Title level='1' weight='semibold' style={{ marginTop: 16, marginBottom: 4, color: 'var(--text_primary)' }}>
								{separateInt(props.fetchedData.earned)}
							</Title>
							<Title level='2' weight='regular' style={{ marginBottom: 16, color: 'var(--dynamic_gray)' }}>
								–ë–∞–ª–∞–Ω—Å
							</Title>
						</center>
					</FixedLayout>

				</div>
			}

		</Panel>
	);
}

export default Home;
